.hangman img {
  border: 10px solid white;
  background: white;
  -webkit-box-shadow: 0 10px 30px rgba(0, 0, 0, 0.27),
    0 0 40px rgba(0, 0, 0, 0.06) inset;
          box-shadow: 0 10px 30px rgba(0, 0, 0, 0.27),
    0 0 40px rgba(0, 0, 0, 0.06) inset;
}

.hangman h1 {
  font-size: 2.5rem;
  font-weight: 100;
  margin-top: 0;
}
.hangman-wrong {
  letter-spacing: .2em;
  font-size: 1rem;
  font-weight: 600;
}

.hangman-word {
  letter-spacing: 1em;
  margin: 0.4em -1em 0.2em 0;
  font-size: 2rem;
  font-weight: 600;
}

.hangman-btns {
  text-align: center;
  display: inline-block;
  width: 320px;
  font-weight: 600;
  letter-spacing: 0.3em;
}

.hangman button {
  border: none;
  font-family: inherit;
  font-size: 1rem;
  cursor: pointer;
  width: 30px;
  margin: 7px 2px 2px 2px;
  letter-spacing: 1px;
  outline: none;
  background: #ffc107;
  color: #fff;
  -webkit-box-shadow: 0 6px #ff9800;
          box-shadow: 0 6px #ff9800;
  border-radius: 5px;
  padding-top: 4px;
}

.hangman button:hover {
  background-color: #ff9800;
}

.hangman button:disabled {
  background: #bdbdbd;
  color: #eeeeee;
  -webkit-box-shadow: 0 6px #9e9e9e;
          box-shadow: 0 6px #9e9e9e;
}

#hangman-reset {
  width: auto;
  display: block;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
  background: #fd7638;
  color: #fff;
  -webkit-box-shadow: 0 6px #ff3300;
          box-shadow: 0 6px #ff3300;
}

#hangman-reset:hover {
  background: #ff3300;
}



